import { useEffect } from 'react';
import axios from 'axios';
import Cookies from 'js-cookie';

const HandleStravaCallback = ({ setUser }) => {
    alert("here!!!")
    useEffect(() => {
        const urlParams = new URLSearchParams(window.location.search);
        const authorizationCode = urlParams.get('code');

        if (authorizationCode) {
            // Exchange authorization code for an access token
            axios.post('/api/get-access-token', { code: authorizationCode })
                .then(async (response) => {
                    const accessToken = response.data.accessToken;

                    // Call getUserInfo with the access token (if you have this function)
                    await getUserInfo(accessToken);

                    // Store the token in a cookie
                    Cookies.set('stravaAccessToken', accessToken, { expires: 0.25 });
                    console.log(`Cookie stored - ${accessToken}`);
                })
                .catch((error) => {
                    console.error('Error fetching access token', error);
                });
        } else {
            console.error('Authorization code not found in URL');
        }
    }, []); // Empty dependency array ensures this only runs once when the component mounts

    const getUserInfo = async (accessToken) => {
        try {
            const response = await axios.get('https://www.strava.com/api/v3/athlete', {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                },
            });
            setUser(response.data)
            console.log('User Information:', response.data);
        } catch (error) {
            console.error('Error fetching user information:', error.response ? error.response.data : error.message);
        }
    };

    return <div>Connecting to Strava...</div>;
};

export default HandleStravaCallback;